.App {
  text-align: center;
}


input {
  	color: #383cc1;
	font-size: 1rem;
	width: 500px;
	margin: 2rem auto;
	display: block;
	padding: 0.5rem 1rem;
	border: 2px solid #383cc1;
	border-radius: 2rem;
  }

  input::placeholder {
	color: #012970;
}
  
input:focus {
	outline: none;
	border: 2px solid #012970;
}