  .card {
    width: 400px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 1px 2px 0 rgba(0, 0, 0, 0.19);
    text-align: center;
    border-radius: 0.35rem;
    margin: 2rem auto;
    background-color: #fff;
  }
  
  .cardContent {
    padding: 1rem 0.2rem 0.2rem 0.2rem;
    color: #012970;
    text-align: center;
    border-bottom-left-radius: 0.35rem;
    border-bottom-right-radius: 0.35rem;
  }
  
  .university {
    text-align: center;
    font-size: 1rem;
    color: #012970;
    margin: 0rem 0 0.9rem 0;
    padding: 0 2rem;
    font-weight: 400;
  }

  .topics {
    font-size: 1rem;
    font-weight: 500;
    padding: 0 0 1rem 0;
  }
  
  .cardFooter {
    border-top: 1px solid rgba(0, 0, 0, 0.125);
    padding: 0.7rem;
    margin: 0 auto 0;
    width: 20rem;
  }
  
  .email {
    font-size: 1rem;
    color: #383cc1;
    text-decoration: none;
    text-align: center;
    display: flex;
    justify-content: center;
  }
  
  .email:hover {
    color: #012970;
  }
  